@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}body{font-family:'Lato', sans-serif;width:100vw;overflow-x:hidden;background-color:#f5f5ff}input,textarea{border:1px solid #a8a8ff;border-radius:15px;padding-left:8px}textarea{width:60%}button,input[type="submit"]{background-color:inherit;border:1px solid #a8a8ff;border-radius:15%;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}input[type="submit"]{border-radius:3px}.data{width:100%}label{margin:0.4rem 0;width:100%;display:flex;justify-content:space-between}.App{width:100vw;display:flex;flex-direction:column;align-items:center}header{width:100%;height:50px;display:flex;justify-content:center;align-items:center;border-bottom:1px solid #a8a8ff;background-color:#c2c2ff}h1{font-size:2rem;letter-spacing:0.3ch;word-spacing:0.6ch}h2{margin-bottom:0.5rem;font-size:1.2rem;text-align:center}h3{margin-bottom:0.4rem}.container{border:1px solid #a8a8ff;border-radius:5px;margin:0.75rem;padding:1rem;width:500px;display:flex;flex-direction:column;background-color:white}.form{margin-bottom:0.5rem;padding:1rem;box-sizing:border-box;border:1px solid #a8a8ff;display:flex;flex-wrap:wrap}.solo-form{display:flex;flex-wrap:wrap}.toast{position:fixed;padding:0.5rem 0.75rem;line-height:1.5;border-radius:3px;width:18%;height:50px;bottom:1rem;left:41%;border:1px solid black;background-color:#a8a8ff;opacity:1;transition:opacity .5s linear 0.5s;display:flex;justify-content:center;align-items:center}.transition{opacity:0}.hide{display:none}.button-container{display:flex;justify-content:center;grid-gap:1rem;gap:1rem}.display{border:1px solid #a8a8ff;margin:0.5rem 0;padding:1rem;box-sizing:border-box;display:flex;flex-wrap:wrap;justify-content:flex-start}.display>*{width:50%;margin-bottom:1rem}.tasks{width:100%}

